import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import AboutView from "../views/AboutView.vue";
import AppGallery from "../views/Gallery.vue";
import AppPlans from "../views/plans.vue";
import trainers from "../views/trainers.vue";
import ContactUs from "../views/contact.vue";

const routes = [
   {
      path: "/",
      name: "home",
      component: HomeView,
   },
   {
      path: "/about",
      name: "about",
      component: AboutView,
   },
   {
      path: "/gallery",
      name: "Gallery",
      component: AppGallery,
   },
   {
      path: "/plans",
      name: "planing",
      component: AppPlans,
   },
   {
      path: "/trainers",
      name: "trainers",
      component: trainers,
   },
   {
      path: "/contact",
      name: "contact-us",
      component: ContactUs,
   },
];

const router = createRouter({
   history: createWebHistory(process.env.BASE_URL),
   routes,
});

export default router;
